import { Inject, Injector } from '@angular/core';
import { HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { ACLService } from '@delon/acl';

import { _HttpClient } from './http.client';

export abstract class BaseApi {
  constructor(@Inject(Injector) protected injector: Injector) {}
}

export interface HttpOptions {
  /** ACLéç½®ï¼è¥å¯¼å¥ `@delon/acl` æ¶èªå¨ææï¼ç­åäº `ACLService.can(roleOrAbility: ACLCanType)` åæ°å¼ */
  acl?: any;
  observe?: 'body' | 'events' | 'response';
  responseType?: 'arraybuffer' | 'blob' | 'json' | 'text';
  reportProgress?: boolean;
  withCredentials?: boolean;
}

interface ParamType {
  key: string;
  index: number;
  [key: string]: any;
  [key: number]: any;
}

const paramKey = `__api_params`;

function setParam(target: any, key = paramKey) {
  let params = target[key];
  if (typeof params === 'undefined') {
    params = target[key] = {};
  }
  return params;
}

/**
 * é»è®¤åºåURL
 * - ææèå´ï¼ç±»
 */
export function BaseUrl(url: string) {
  return function<TClass extends { new (...args: any[]): BaseApi }>(
    target: TClass,
  ): TClass {
    const params = setParam(target.prototype);
    params.baseUrl = url;
    return target;
  };
}

/**
 * é»è®¤ `headers`
 * - ææèå´ï¼ç±»
 */
export function BaseHeaders(
  headers:
    | HttpHeaders
    | {
        [header: string]: string | string[];
      },
) {
  return function<TClass extends { new (...args: any[]): BaseApi }>(
    target: TClass,
  ): TClass {
    const params = setParam(target.prototype);
    params.baseHeaders = headers;
    return target;
  };
}

function makeParam(paramName: string) {
  return function(key?: string, ...extraOptions: any[]) {
    return function(target: BaseApi, propertyKey: string, index: number) {
      const params = setParam(setParam(target), propertyKey);
      let tParams = params[paramName];
      if (typeof tParams === 'undefined') {
        tParams = params[paramName] = [];
      }
      tParams.push({
        key,
        index,
        ...extraOptions,
      });
    };
  };
}

